      {path:require('@/assets/img/work-img/work3.png')},
      {path:require('@/assets/img/work-img/work4.png')},
      {path:require('@/assets/img/process/process-thumb.png')},
      {path:require('@/assets/img/team/team1.png')},
      {path:require('@/assets/img/team/team2.png')},
      {path:require('@/assets/img/team/team4.png')},
      {path:require('@/assets/img/sponsor-img/sponsor1.png')},
      {path:require('@/assets/img/sponsor-img/sponsor2.png')},
      {path:require('@/assets/img/sponsor-img/sponsor3.png')},
