<script>
export default {
  data(){
    return{
        accordionItems:[
          {id:1,text:'我如何知道我需要什么样的视频？'},
          {id:2,text:'你目前有职业空缺吗？'},
          {id:3,text:'制作动画视频需要多长时间？'},
          {id:4,text:'视频制作过程需要多长时间？'},
          {id:5,text:'我会参与创建过程吗？'},
          {id:6,text:'我可以订购哪种类型/格式的动画视频？'},
          {id:7,text:'我要怎么联系到您？'}],
        contactSpan:10,
        formSpan:12,
        imgPath:[
        {path:require('@/assets/img/banner/left-man.png')},
        ]
    }
  },
    methods:{
      listenPage(){
      let a = window.innerWidth
     if(a>1000){
    this.contactSpan= 10
    this.formSpan=12
     }else if(a>710 && a<=1000){
    this.contactSpan= 24
       this.formSpan=24
     }else if(a<=710){
    this.contactSpan= 24
      this.formSpan=24
     }else{
   this.contactSpan= 24
     this.formSpan=24
     }
     },
  },
    created(){
  this.listenPage()
  // 监听页面大小
  window.addEventListener('resize',() => {
     this.listenPage()
  });
},
mounted(){
 const dt = document.querySelectorAll('dt')
        const dl = document.querySelectorAll('dl')
        const dd = document.querySelectorAll('dd')
        for (let k = 0; k < dt.length; k++) {

            dt[k].addEventListener('click', function() {
                // 判断，如果当前点击标题存在类，就消除
                if (dl[k].children[1].className) {
                    this.className = ''
                    for (let j = 1; j < dl[k].children.length; j++) {
                        dl[k].children[j].className = ''
                    }
                } else {
                    // 排他思想 手风琴效果,消除所有类
                    for (let l = 0; l < dd.length; l++) {
                        dd[l].className = ''
                    }
                    for (let p = 0; p < dt.length; p++) {
                        dt[p].className = ''
                    }
                    // 为当前点击的一级标题添加类
                    for (let i = 1; i < dl[k].children.length; i++) {
                        dl[k].children[i].className = 'block'
                    }
                    this.className = 'title'
                }
            })
        }
}


}
</script>

