<template>
   <div class="box" v-if="closeAnimate" :style="{top:topVal+'px'}">
      <img src="@/assets/img/breadcumnd/rocket-banner.png" alt="" class="FactoryAnimation">
    </div>
</template>

<script>
export default {
 data(){
    return{
      topVal:0,
      closeAnimate:true
    }
  },
  created(){
       this.topVal = Math.floor(window.scrollY)
    setTimeout(() => {
      this.closeAnimate=false
    },1000)
    window.addEventListener('scroll',() => {
      this.topVal = Math.floor(window.scrollY)
    })
  },
}
</script>

