    mounted() {
        
    　　// 绑定滚动事件
    　　window.addEventListener('scroll', this.handleScroll)
   },
    methods: {
      // 点击回调home页面
      toHome(){
        this.$router.push('/home')
      },
      // 点击按钮切换字体图标
      changeIcoBtn(){
        if(this.whichIco){
          this.whichIco = !this.whichIco
        }else{
           this.whichIco = !this.whichIco
        }    
      },
      //  获取滚动距离
        handleScroll() {
         if(window.pageYOffset>=300){
          this.ifFixed=true
         }else{
           this.ifFixed=false
         }
        } 
    },

}
