      {path:require('@/assets/img/sponsor-img/sponsor6.png')},
      ]
    }
  },
  methods:{
    showBtn(val){
     if(val== 'all'){
        for (const key in this.btnGroup) {
             this.btnGroup[key]=true
       } 
   }else{
       for (const key in this.btnGroup) {
            if(key==val){
             this.btnGroup[key]=true
             continue
            }
             this.btnGroup[key]=false
       }
          }
    },
  listenPage(){
      let a = window.innerWidth
    //  lg >1220
    //  md <=1220
  //  sm <=915
  //  xs <=610
     if(a>915 && a<=1200){
    this.studioCarouselHeight=190
    this.homeProductionsSpan= 10
     }else if(a>610 && a<=915){
    this.studioCarouselHeight=280
    this.homeProductionsSpan= 10
     }else if(a<=610){
    this.studioCarouselHeight=280
    this.homeProductionsSpan= 20
     }else{
    this.studioCarouselHeight=90
   this.homeProductionsSpan= 10
     }
},
addClass(e){
     switch (e) {
            case 4:
            this.$refs.playBoxRef4.className = 'play_box'
                break;
                  case 5:
            this.$refs.playBoxRef5.className = 'play_box'
                break;
             
               case 6:
            this.$refs.playBoxRef6.className = 'play_box'
                break;
             
               case 7:
            this.$refs.playBoxRef7.className = 'play_box'
                break;      
                       case 8:
             this.$refs.playBoxRef8.className = 'play_box'
                break;      
                   case 9:
             this.$refs.playBoxRef9.className = 'play_box'
                break;      
                   case 10:
             this.$refs.playBoxRef10.className = 'play_box'
                break;      
                   case 11:
             this.$refs.playBoxRef11.className = 'play_box'
                break;    
        }
  },
   removeClass(e){
      switch (e) {
            case 4:
          this.$refs.playBoxRef4.className = ''
                break;
                  case 5:
            this.$refs.playBoxRef5.className = ''
                break;
             
               case 6:
             this.$refs.playBoxRef6.className = ''
                break;
             
               case 7:
             this.$refs.playBoxRef7.className = ''
                break;      
                   case 8:
             this.$refs.playBoxRef8.className = ''
                break;      
                   case 9:
             this.$refs.playBoxRef9.className = ''
                break;      
                   case 10:
             this.$refs.playBoxRef10.className = ''
                break;      
                   case 11:
             this.$refs.playBoxRef11.className = ''
                break;      
        }
  },
  },
  created(){
  this.listenPage()
  // 监听页面大小
  window.addEventListener('resize',() => {
     this.listenPage()
  });
}

