import App from './App.vue'
import router from './router'

// // 导入element ui 样式
import 'element-ui/lib/theme-chalk/index.css';
import 'element-ui/lib/theme-chalk/display.css';


//引入懒加载插件
import VueLazyload from 'vue-lazyload';

// 导入开场动画组件
import Animate from './components/FactoryAnimation.vue'
// 全局注册动画效果组件
Vue.component('MyAnimate', Animate)

//注册懒加载
Vue.use(VueLazyload);


new Vue({
    router,
    render: h => h(App)
}).$mount('#app')