       homeProductionsSpan:10,
        btnGroup:{
        Animation:true,
         Animation_2D:true,
        Animation_3D:true,
         Visual_trends:true,
         Creativity:true
      },
      imgPath:[
        {path:require('@/assets/img/breadcumnd/rocket-banner.png')},
        {path:require('@/assets/img/blog/blog1.png')},
        {path:require('@/assets/img/blog/blog2.png')},
        {path:require('@/assets/img/blog/blog3.png')},
        {path:require('@/assets/img/blog/blog4.png')},
        {path:require('@/assets/img/blog/blog5.png')},
        {path:require('@/assets/img/blog/blog6.png')},
      ]
    }
  },
  methods:{
        prevPage(){
   this.$refs.processRef.prev()
  },
    nextPage(){
   this.$refs.processRef.next()
  },
     showBtn(val){
     if(val == 'all'){
        for (const key in this.btnGroup) {
             this.btnGroup[key]=true
       } 
   }else{
       for (const key in this.btnGroup) {
            if(key==val){
             this.btnGroup[key]=true
             continue
            }
             this.btnGroup[key]=false
       }
          }
    },
      listenPage(){
      let a = window.innerWidth
    //  lg >1220
    //  md <=1220
  //  sm <=915
  //  xs <=610
     if(a>915 && a<=1200){
    this.homeProductionsSpan= 10
     }else if(a>710 && a<=915){
    this.homeProductionsSpan= 10
     }else if(a<=710){
    this.homeProductionsSpan= 18
     }else{
   this.homeProductionsSpan= 10
     }
     },
  },
    created(){
  this.listenPage()
  // 监听页面大小
  window.addEventListener('resize',() => {
     this.listenPage()
  });
}
}
</script>

